<template>
	<div class="body">
		<div class="company_name flex_between">
			<div class="company_name_left">
				<i class="icon iconfont icon-gongsixiangqinglogo"></i>
				<span>新增项目</span>
			</div>
		</div>
		<div class="form_box">
			<div class="form flex_between form_bottom">
				<div class="form_list flex_start">
					<div class="form_list_left">
						项目名称
					</div>
					<div class="form_list_right">
						<el-input placeholder="请填写" v-model="commitData.project_name" clearable>
						</el-input>
					</div>
				</div>
				<div class="form_list flex_start">
					<div class="form_list_left">
						项目企业名称
					</div>
					<div class="form_list_right">
						<el-select  v-model="tianyan_id" filterable remote clearable reserve-keyword placeholder="请填写" :remote-method="remoteMethod" :loading="loading" @change="getProjectCompany($event)">
							<el-option v-for="option in projectCompanyList"  :key="option.id" :label="option.name" :value="option.id"  >
							</el-option>
						</el-select>
					</div>
					
				</div>
				<div class="form_list flex_start">
					<div class="form_list_left">
						币种
					</div>
					<div class="form_list_right">
						<el-select v-model="commitData.unit" clearable placeholder="请选择">
							<el-option v-for="item in moneyTypeList" :key="item.id" :label="item.name" :value="item.id">
							</el-option>
						</el-select>
					</div>
				</div>
				<div class="form_list flex_start">
					<div class="form_list_left">
						项目经理
					</div>
					<div class="form_list_right">
						<el-select v-model="commitData.project_manager_id" clearable  filterable placeholder="请选择">
							<el-option v-for="item in creatorList" :key="item.id" :label="item.name" :value="item.id">
							</el-option>
						</el-select>
					</div>
				</div>
				<div class="form_list flex_start">
					<div class="form_list_left">
						行业
					</div>
					<div class="form_list_right">
						<el-input placeholder="请填写" v-model="commitData.industry" clearable>
						</el-input>
					</div>
				</div>
				<div class="form_list flex_start">
					<div class="form_list_left">
						项目负责人
					</div>
					<div class="form_list_right">
						<el-select v-model="commitData.project_charge_id" clearable  filterable placeholder="请选择">
							<el-option v-for="item in creatorList" :key="item.id" :label="item.name" :value="item.id">
							</el-option>
						</el-select>
					</div>
				</div>
				<div class="form_list flex_start">
					<div class="form_list_left">
						项目状态
					</div>
					<div class="form_list_right">
						<el-select v-model="commitData.project_status" clearable  filterable placeholder="请选择">
							<el-option v-for="item in projectStatusList" :key="item.id" :label="item.status" :value="item.id">
							</el-option>
						</el-select>
					</div>
				</div>
				<div class="form_list flex_start">
					<div class="form_list_left">
						项目倍数
					</div>
					<div class="form_list_right">
						<el-input placeholder="请填写" v-model="commitData.multiple" clearable>
						</el-input>
					</div>
				</div>
				<div class="form_list flex_start">
					<div class="form_list_left">
						是否舆情监测
					</div>
					<div class="form_list_right flex_start">
						<div @click="changeHideStatus(1)" class="radio_btn cursor" :class="[commitData.is_monitor?'radio_btn_active':'']">
							是
						</div>
						<div @click="changeHideStatus(2)" class="radio_btn cursor" :class="[!commitData.is_monitor?'radio_btn_active':'']">
							否
						</div>
					</div>
				</div>
				<div class="form_list flex_start">
					<div class="form_list_left">
						项目简介
					</div>
					<div class="form_list_right form_list_right_textarea">
						<el-input placeholder="请填写"  type="textarea" :rows="3" v-model="commitData.discription">
						</el-input>
					</div>
				</div>
			</div>
			<!-- 列表数据2 -->
			<div class="form flex_between form_bottom" v-for="(item,index) of commitData.quote_infos" >
				<div class="form_list flex_start">
					<div class="form_list_left">
						项目企业股票代码
					</div>
					<div class="form_list_right">
						<el-input placeholder="请填写" v-model="item.share_code" clearable>
						</el-input>
					</div>
				</div>
				<div class="form_list flex_start">
					<div class="form_list_left">
						上市/挂牌板块
					</div>
					<div class="form_list_right">
						<el-select v-model="item.list_plate_id" clearable filterable placeholder="请选择">
							<el-option v-for="item in plateList" :key="item.id" :label="item.name" :value="item.id">
							</el-option>
						</el-select>
					</div>
				</div>
				<div class="form_list flex_start">
					<div class="form_list_left">
						上市时间
					</div>
					<div class="form_list_right">
						<el-date-picker v-model="item.quoted_date" type="date" value-format="yyyy-MM-dd" placeholder="选择日期" :editable='false'>
    					</el-date-picker>
					</div>
				</div>
				<div class="form_list flex_start">
					<span @click="deleteData(index,2)" v-if="commitData.quote_infos.length>1">删除</span>
					<div class="table-group-title-btn btn-blue-border" @click="addData(2)" v-if="index==commitData.quote_infos.length-1" >
						 &nbsp;&nbsp;新增数据&nbsp;&nbsp;
					</div>
				</div>
			</div>

			<!-- 列表数据3 -->
			
			<div class="form flex_between form_bottom" v-for="(item,index) of commitData.contacts" >
				<div class="realation_title">
					项目公司联系人
				</div>
				<div class="form_list flex_start">
					<div class="form_list_left">
						姓名
					</div>
					<div class="form_list_right">
						<el-input placeholder="请填写" v-model="item.name" clearable >
						</el-input>
					</div>
				</div>
				<div class="form_list flex_start">
					<div class="form_list_left">
						手机
					</div>
					<div class="form_list_right">
						<el-input placeholder="请填写" v-model="item.tel" clearable maxlength='11'>
						</el-input>
					</div>
				</div>
				<div class="form_list flex_start">
					<div class="form_list_left">
						身份证号
					</div>
					<div class="form_list_right">
						<el-input placeholder="请填写" v-model="item.identify_id" clearable maxlength='18'>
						</el-input>
					</div>
				</div>
				<div class="form_list flex_start">
					<div class="form_list_left">
						邮箱
					</div>
					<div class="form_list_right">
						<el-input placeholder="请填写（选填）" v-model="item.email" clearable>
						</el-input>
					</div>
				</div>
				<div class="form_list flex_start">
					<div class="form_list_left">
						其他
					</div>
					<div class="form_list_right">
						<el-input placeholder="请填写" v-model="item.other_info" clearable>
						</el-input>
					</div>
				</div>
				<div class="form_list flex_start">
					<span @click="deleteData(index,3)" v-if="commitData.contacts.length>1">删除</span>
					<div class="table-group-title-btn btn-blue-border" @click="addData(3)" v-if="index==commitData.contacts.length-1" >
						 &nbsp;&nbsp;新增数据&nbsp;&nbsp;
					</div>
				</div>
			</div>
		</div>
		
		<!--表单按钮模块-->
		<div class="form_submit flex_center">
			<button @click="cancleAddSubFundProject()">取消</button>
			<button @click="toAddSubFundProject()">保存</button>
		</div>
	</div>
</template>

<script>
	import {getUserlistApi,getcomPany,} from '../../config/api'
	import {getSubscriptionList,getQuotedPlace,directAddFundProject,getProjectStatusList} from '../../config/apiSj'
	import {dialogConfirm,dialogConfirmHasbtn,isValidTel,isValidIdentify} from '../../config/mUtils'
	export default {
		data() {
			return {
				projectCompanyList:[],//项目企业名称列表
				loading: false,
				tianyan_id:'',//天眼查id

				moneyTypeList:[
					{
						id:1,
						name:'人民币'		
					},
					{
						id:2,
						name:'美元'		
					},
				],//币种列表
				creatorList:[],//项目经理列表
				projectStatusList:[],//项目状态列表
				plateList:[],//上市/挂牌板块列表

				commitData:{
					project_name:'',//项目名称
					project_com_name:'',//项目企业名称
					unit:1,//币种 
					project_manager_id:'',//项目经理id
					industry:'',//行业
					// quit_info:'',//退出情况
					is_monitor:1,
					project_status:'',//项目状态
					quote_infos:[
						{
							share_code:'',
							quoted_date:'',
							list_plate_id:'',
						}
					],
					contacts:[
						{
							name:'',
							tel:'',
							identify_id:'',
							email:'',
							other_info:'',
						}
					],
					tianyan_id:'',//天眼查id
					multiple:'',
				},
			}
			
		},
		created() {
			this.getModuleInfo();
			this.getAllUserList();
			this.toGetProjectStatusList();
		},
		mounted() {},
		components: {},
		methods: {
			//修改舆情状态
			changeHideStatus(e){
				if (e == 1) {
					this.commitData.is_monitor = 1;
				} else{
					this.commitData.is_monitor = 0;
				}
			},
			//获取上市/挂牌模板信息
			getModuleInfo(){
				//请求接口
				getQuotedPlace({
					keyword:'',
				}).then(res => {
					if (res.data.msgNo == 1) {
						let data = res.data.payload ;
						this.plateList = data;
					}
				})
			},
			// 获取数据创建人列表
			getAllUserList(){
				getUserlistApi({
					number: 10000,
					page: 1
				}).then(res => {
					if(res.data.msgNo == 1) {
						this.creatorList = res.data.payload.data;
						this.commitData.project_manager_id = parseInt( window.localStorage.getItem('htcx_uid')) ;
					}
				})
			},
			// 获取项目状态列表
			toGetProjectStatusList(){
				getProjectStatusList().then(res => {
					if(res.data.msgNo == 1) {
						// console.log(res);
						this.projectStatusList = res.data.payload;
						this.projectStatusList = this.projectStatusList.splice(0,3)
						if(this.projectStatusList.length>0){
							this.commitData.project_status= this.projectStatusList[0].id;
						}
						
					}
				})
			},
			//添加项目企业挂牌信息/认购基金/联系人
			addData(type){
				if(type === 1){
                    this.commitData.subscription_infos.push({
						subscription_fund_type:'',
						subscription_fund_ids:[],
						subscribeFundList:[],
					});
                }else if(type === 2){
                    this.commitData.quote_infos.push({
						share_code:'',
						quoted_date:'',
						list_plate_id:'',
					});
                }else if(type === 3){
                    this.commitData.contacts.push({
						name:'',
						tel:'',
						identify_id:'',
						email:'',
						other_info:'',
					});
                }
				
			},
			//删除项目企业挂牌信息/认购基金/联系人
			deleteData(index,type){
				if(type === 1){
                    this.commitData.subscription_infos.splice(index,1);
                }else if(type === 2){
                    this.commitData.quote_infos.splice(index,1);
                }else if(type === 3){
                    this.commitData.contacts.splice(index,1);
                }
			},
			//取消添加子基金项目
			cancleAddSubFundProject(){
				this.$router.push({
					name: 'directInvestProjectHome',
				})
			},
			//添加子基金项目
			toAddSubFundProject(){
				// console.log(this.commitData);
				if(this.commitData.project_name&&this.commitData.project_com_name){
					//处理数据
					let commitData = JSON.parse(JSON.stringify(this.commitData));
					let canContinue = true;
					commitData.contacts.forEach((element,index) => {
						if(canContinue){
							if(element.tel&&canContinue){
								if(!isValidTel(element.tel)){
									dialogConfirm('info', '手机号码格式错误!');
									canContinue = false;
								}
							}
							if(element.identify_id&&canContinue){
								if(!isValidIdentify(element.identify_id)){
									dialogConfirm('info', '身份证号格式错误!');
									canContinue = false;
								}
							}
						}
						
					});
					if(!canContinue){
						return false;
					}
					commitData.quote_infos = JSON.stringify(commitData.quote_infos);
					commitData.contacts = JSON.stringify(commitData.contacts);
					directAddFundProject(commitData).then(res => {
						if (res.data.msgNo == 1) {
							let data  = res.data.payload;
							dialogConfirm('success', '保存成功');
							//跳转列表页面
							this.$router.push({
								name: 'directInvestProjectHome',
							})
						}else{
							dialogConfirm('info', res.data.message);
						}
					})
				}else{
					if (!this.commitData.project_name) {
						dialogConfirm("info", '项目名称不能为空');
					}else if(!this.commitData.project_com_name){
						dialogConfirm("info", '项目企业名称不能为空');
					}
				}
			},
			//请求天眼查
			remoteMethod(query) {
				if(query !== '') {
					this.loading = true;
					getcomPany({
						name: query
					}).then(res => {
						this.loading = false;
						if(res.data.msgNo == 1) {
							if(res.data.payload.length == 0) {
								this.projectCompanyList = [{
									name: query,
									tianyan_id:0,
								}]
							} else {
								let newObj = {
									name: query,
									tianyan_id:0,
								}
								res.data.payload.push(newObj);
								this.projectCompanyList = res.data.payload;
							}

						}
					})
				} else {
					this.projectCompanyList = [];
				}
			},
			getProjectCompany(e){
				// console.log(e);
				// console.log(this.projectCompanyList);
				this.projectCompanyList.forEach((element,index) => {
					if(element.id==e){
						this.commitData.tianyan_id = element.id;
						this.commitData.project_com_name = element.name;
					}
				});
			}
		},
	}
</script>

<style lang="scss" scoped="scoped">
	.body {
		padding: 0 30px 30px 30px;
		font-size: 14px;
	}
	.company_name {
		height: 72px;
		border-bottom: 1px solid #EAEAEA;
	}
	.company_name_left {
		font-size: 18px;
		color: #33302D;
	}
	.company_name_left i {
		margin-right: 3px;
		color: #6292F4;
	}
	.form_box{
	}
	.form{
		flex-wrap: wrap;
		color: #6F7071;
		padding: 30px 0 10px;
	}
	.form_bottom{
		border-bottom:1px solid #E5E5EA;
	}
	.form_list{
		display: inline-flex;
		margin-bottom: 20px;
		&>span{
			font-size: 14px;
			color: #6292F4;
			cursor: pointer;
		}
		&>button{
			width: 100px;
			height: 30px;
			color: #6292F4;
			font-size: 14px;
			border-radius: 15px;
			background-color: white;
			margin-left: 46px;
			border: 1px solid #6292F4;
		}
	}
	.form_list_left{
		width: 130px;
		color: #6F7071;
	}
	.form_list_right{
		width: 260px;
	}
	.form_list_right_textarea{
		width: 800px;
	}
	.radio_btn{
		width: 73px;
		height: 40px;
		margin-right: 38px;
		line-height: 40px;
		text-align: center;
		border-radius: 20px;
		background: #F3F6FB;
	}
	.radio_btn_active{
		color: white;
		background: #6292F4 !important;
	}
	.form_submit{
		margin-top: 40px;
	}
	.form_submit button{
		width: 94px;
		height: 36px;
		color: #6292F4;
		font-size: 14px;
		margin-right: 26px;
		border-radius: 20px;
		border: 1px solid #6292F4;
		
	}
	.form_submit button:nth-child(2){
		color: white;
		background: #6292F4;
	}

	.realation_title{
		width: 100%;
		font-size: 16px;
		color: #33302D;
		margin-bottom: 20px;
	}
</style>


